.level-modal {
    position: fixed;
    width: 100%;
    left: 0;
    right: 0;
    margin: auto;
    top: 0;
    height: 100%;
    background-color: #090304;
    display: flex;
    flex-direction: column;
    padding: 60px 15px;
    align-items: center;
    max-width: 500px;
    transition-property: transform;
    transition-duration: 290ms;
    transform: scale3d(2.1, 1.5, 1)translate3d(50px,110px,0px);
    opacity: 0;
    visibility: hidden;
    &.active {
        transform: scale3d(1, 1, 1)translate3d(0px,0px,0px);
        opacity: 1;
        visibility: visible;
    }
    h6 {
        color: #00b2a9;
        display: flex;
        font-size: 18px;
        font-family: SpaceGrotesk;
        background-color: #090304;
        padding: 0 10px;
        &:before {
            content: "";
            position: absolute;
            right: 0;
            width: 100%;
            height: 1px;
            background-color: #00b2a9;
            margin-top: 11px;
            z-index: -1;
        }
    }
    div {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 50px 0 ;
        max-width: 295px;
        span {
            display: flex;
            width: 50px;
            height: 50px;
            font-family: SpaceGrotesk;
            background-color: rgb(0 178 169 / 30%);
            align-items: center;
            justify-content: center;
            cursor: pointer;
            margin: 8px 0px;
            color: #fff;
            box-shadow: 0 0 0 1px rgb(255 255 255 / 50%);
            position: relative;
            &.soon {
                opacity: 0.35;
                cursor: not-allowed;
            }
        }

    }
}